

.dialogs {
display: grid;
    grid-template-columns: 2fr 3fr 1fr 3fr;
    grid-template-rows: 70px 1fr;
    grid-row-gap: 40px;
    grid-column-gap: 20px;


}
.dialogs .h1 {
    grid-row: 1 ;
    grid-column: 1/5;
    text-align: left;
    background-color:springgreen;
    padding-bottom: 50px;
    padding-left: 35px;
}



.dialogItems  {
    padding: 10px;
    padding-top: 0px;
    grid-row: 2;
    grid-column: 1;
    display: inline-block;

}


.dialog {
    padding-top:10px;

    padding-left: 5px;
    border-bottom: solid black ;
    border-top: solid black ;
    margin-top: 5px;
    font-weight: bold;
    font-size: large;
    background-color: springgreen;
    vertical-align: middle;



}

.dialog .active {

    font-size: x-large;
    color: #1a4780;
}

.messages {
padding-top: 0px;
    grid-column: 2/5;
    border: double 7px #1a4780;
}


.dialog a{
    text-decoration-line: none;
    color: black;
    font-family: "Comic Sans MS";
    font-style: italic ;
}


.inputButton{
    display: block;
    padding: 0;
    border: none;
    background: none;
    border: 0px;
    outline: none !important ;
}
.inputWindow {
    display: block;
    border: solid 2px #1a4780;
    float: left;
    height: 40px;
    margin: -1px -2px -2px;
    width: 480px;
    margin-bottom: 20px;
    margin-top: 50px;
    margin-left: 20px;
    outline: none !important ;
    line-break: auto;




}

.messages {
}

.inputImg {
    display: block;
    float: left;
    width: 45px;
    margin-top: 50px;
    margin-left: 10px;
    outline: none !important ;
}
.chat {
    height: 400px;
    overflow: auto; /* Это позволяет отображать полосу прокрутки */
    position: relative; /* Это позволяет съезжать тексту в слое, не растягивая страницу */
    overflow-scrolling: revert;

}

.avatar {
    height: 50px;
    margin-right: 20px;
    border-radius: 50%;
    vertical-align: middle;
    margin-bottom: 10px;

}

